export default {
	data() {
		return {
			id:'',
			title:'',
			time:'',
			name:'',
			arrange:'',
			// tips:''
		};
	},
	onLoad(o){
		this.id = o.id;
	},
	onShow() {
		
		this.getinfo();
	},
	methods:{
		getinfo(){
			uni.request({
				url: this.$api+'/weixin/shishenxin/noticedetaile',
				data: this.getmd5({
					openId:uni.getStorageSync('openId'),
					id:this.id
				}),
				header: {
					'content-type': 'application/json'
				},
				method: "POST",
				success: (res) => {
					if (res.data.code != 200) {
						uni.showToast({
						icon: 'none',
						title: res.data.msg,
						duration: 2000
						});
					} else {
						uni.hideLoading();
						var data = res.data.data;
						this.title = data.informName;
						this.time = data.informTime;
						this.name = data.createName;
						this.arrange = data.content;
					}
				},
				fail: () => {
					uni.hideLoading();
					uni.showToast({
					icon: 'none',
					title: '获取失败',
					duration: 2000
					});
				}
			});
		},
	}
	
}
